body {
  background-color: #ffeef8; /* Light pink background */
  font-family: 'Arial', sans-serif;
}

.App {
  text-align: center;
  margin: 20px;
}

.todo {
  background-color: #ffb3d9; /* Pink for individual tasks */
  border-radius: 10px;
  padding: 10px;
  margin: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

button {
  background-color: #ff66b2; 
  color: white;
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
  cursor: pointer;
}

button:hover {
  background-color: #ff3399; 
}

input {
  border: 1px solid #ff66b2;
  border-radius: 5px;
  padding: 5px;
  margin: 10px 0;
}

h1 {
  color: #d5006d;
}
